import * as React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';

import { alpha, styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import Tooltip from '@mui/material/Tooltip';
import TableCell from '@mui/material/TableCell';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import TableSortLabel from '@mui/material/TableSortLabel';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import FilterListIcon from '@mui/icons-material/FilterList';
import FilterListOffIcon from '@mui/icons-material/FilterListOff';
import { visuallyHidden } from '@mui/utils';

import Loader from '../components/Loader';

import { DriversQuery, DriverDelete } from '../api';
import { useSnackbar } from '../hooks';
import { Order, Driver } from '../utils/dataTypes';
import { getType, getPath } from '../utils/queryHelpers';
import { stopPropagation } from '../utils/stopPropagation';

interface HeadCell {
  id: string;
  disablePadding: boolean;
  orderField?: keyof Driver;
  getData: (row: Driver) => { data: JSX.Element | string; title?: string };
  label: string;
  numeric: boolean;
}

const headCells: readonly HeadCell[] = [
  {
    id: 'fullName',
    orderField: 'fullName',
    getData: (row) => ({ data: `${row.fullName}` }),
    numeric: false,
    disablePadding: true,
    label: 'First name/Last name',
  },
  {
    id: 'type',
    getData: (row) => {
      const type = `${getType(row)} `;
      let title = '';
      if (row.type === 'Driver') {
        title = 'Driver';
      }
      if (row.type === 'OwnerDriver') {
        title = 'Owner/Driver';
      }
      if (row.type === 'CoordinatorDriver') {
        title = 'Coordinator/Driver';
      }
      return {
        data: (
          <span>
            <b>{type}</b>
          </span>
        ),
        title: title,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Type',
  },
  {
    id: 'phone',
    getData: (row) => {
      const firstRow = `${row.phone}`;
      const secondRow = `${row.phone2 ? row.phone2 : ''}`;
      return {
        data: (
          <span>
            <a
              href={`tel:${firstRow}`}
              target="_blank"
              onClick={stopPropagation}
            >
              {firstRow}
            </a>
            <br />
            <a
              href={secondRow ? `tel:${secondRow}` : undefined}
              target="_blank"
              onClick={stopPropagation}
            >
              {secondRow || ' '}
            </a>
          </span>
        ),
        title: firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Mobile phone',
  },
  {
    id: 'email',
    getData: (row) => {
      if (row.email) {
        const firstRow = `${row.email}`;
        return {
          data: (
            <span>
              <a
                href={`mailto:${firstRow}`}
                target="_blank"
                onClick={stopPropagation}
              >
                {firstRow}
              </a>
            </span>
          ),
          title: firstRow,
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: true,
    label: 'Email',
  },
  {
    id: 'address',
    getData: (row) => {
      if (row.address) {
        const firstRow = `${row.address}`;
        return {
          data: <span>{firstRow}</span>,
          title: firstRow,
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: true,
    label: 'Home address',
  },
  {
    id: 'onTruck',
    getData: (row) => {
      if (row.driveTrucks && row.driveTrucks.length) {
        const truck = row.driveTrucks[0];
        return {
          data: (
            <span>
              <a
                href={`/trucks/view/${truck.id}`}
                target="_blank"
                onClick={stopPropagation}
              >
                {`${truck.truckNumber}`}
              </a>
              <br />
              <a
                href={
                  truck.owner
                    ? `/${getPath(truck.owner)}/view/${truck.owner.id}`
                    : undefined
                }
                target="_blank"
                onClick={stopPropagation}
              >
                {truck.owner ? `${truck.owner.fullName}` : ' '}
              </a>
            </span>
          ),
          title: `${truck.truckNumber}${
            truck.owner ? `\n${truck.owner.fullName}` : ''
          }`,
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: true,
    label: 'ON TRUCK',
  },
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof Driver,
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order | undefined;
  orderBy: string | undefined;
  rowCount: number;
}

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundColor: theme.palette.background.default,
}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  maxWidth: '100px',
  paddingTop: '6px',
  paddingBottom: '6px',
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
  overflow: 'hidden',
}));

function EnhancedTableHead(props: EnhancedTableProps) {
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
  } = props;
  const createSortHandler =
    (property: keyof Driver) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <StyledTableHead>
      <TableRow>
        <StyledTableCell padding="checkbox">
          <Checkbox
            name={'enhanced-table-checkbox-select-all'}
            color="primary"
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{
              'aria-label': 'enhanced-table-checkbox-select-all',
            }}
          />
        </StyledTableCell>
        {headCells.map((headCell) => (
          <StyledTableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'center'}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={headCell.orderField && orderBy === headCell.orderField}
              hideSortIcon={!headCell.orderField}
              direction={orderBy === headCell.orderField ? order : 'asc'}
              onClick={
                headCell.orderField
                  ? createSortHandler(headCell.orderField)
                  : () => {
                      return;
                    }
              }
            >
              {headCell.label}
              {headCell.orderField && orderBy === headCell.orderField ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </StyledTableCell>
        ))}
      </TableRow>
    </StyledTableHead>
  );
}

interface EnhancedTableToolbarProps {
  selected: readonly string[];
  setQueryParams: React.Dispatch<
    React.SetStateAction<Record<string, number | string>>
  >;
}

function EnhancedTableToolbar(props: EnhancedTableToolbarProps) {
  const { selected, setQueryParams } = props;
  const [fullNameSearch, setFullNameSearch] = React.useState<string>('');
  const [phoneSearch, setPhoneSearch] = React.useState<string>('');
  const [truckNumberSearch, setTruckNumberSearch] = React.useState<number>(0);
  const [searchChanged, setSearchChanged] = React.useState<boolean>(false);
  const location = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();

  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(DriverDelete, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Deleted',
      });
      queryClient.invalidateQueries(['drivers']);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const fullNameHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    setFullNameSearch(event.target.value);
    setSearchChanged(true);
  };
  const phoneHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    setPhoneSearch(event.target.value);
    setSearchChanged(true);
  };
  const truckNumberHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (Number.isFinite(Number(event.target.value))) {
      setTruckNumberSearch(Number(event.target.value));
      setSearchChanged(true);
    }
  };
  const filterClearHandler = () => {
    setSearchChanged(false);
    setFullNameSearch('');
    setPhoneSearch('');
    setTruckNumberSearch(0);
    setQueryParams({});
  };
  const filterHandler = () => {
    setSearchChanged(false);
    const queryParams: Record<string, number | string> = {};
    if (fullNameSearch) {
      queryParams.fullName = fullNameSearch;
    }
    if (phoneSearch) {
      queryParams.allPhone = phoneSearch;
    }
    if (truckNumberSearch) {
      queryParams.truckNumber = truckNumberSearch;
    }
    setQueryParams(queryParams);
  };
  const keyUpHandler = (event: React.KeyboardEvent) => {
    if (event.key !== 'Enter') {
      return;
    }
    if (fullNameSearch || phoneSearch || truckNumberSearch) {
      filterHandler();
    } else {
      filterClearHandler();
    }
  };
  const createHandler = () => {
    navigate('./create', {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const editHandler = () => {
    navigate(`./edit/${selected[0]}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const deleteHandler = () => {
    mutate(`${selected[0]}`);
  };

  return (
    <Toolbar
      sx={{
        overflow: 'hidden',
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        ...(selected.length > 0 && {
          bgcolor: (theme) =>
            alpha(
              theme.palette.primary.main,
              theme.palette.action.activatedOpacity,
            ),
        }),
      }}
    >
      {selected.length > 0 ? (
        <Typography
          sx={{ flex: '1 1 100%' }}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {selected.length} selected
        </Typography>
      ) : (
        <Typography
          sx={{
            flex: '1 1 100%',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            overflow: 'hidden',
          }}
          variant="h6"
          id="tableTitle"
          component="div"
        >
          Drivers
        </Typography>
      )}
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          label="First name/Last name"
          value={fullNameSearch}
          onChange={fullNameHandler}
          onKeyUp={keyUpHandler}
          size="small"
        />
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          label="Phone"
          value={phoneSearch}
          onChange={phoneHandler}
          onKeyUp={keyUpHandler}
          size="small"
        />
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          label="Truck Number"
          value={truckNumberSearch || ''}
          onChange={truckNumberHandler}
          onKeyUp={keyUpHandler}
          size="small"
        />
      </Box>
      <Tooltip title="Filter list">
        {(searchChanged ||
          (!fullNameSearch && !truckNumberSearch && !phoneSearch)) &&
        (!(searchChanged && !fullNameSearch) ||
          !(searchChanged && !truckNumberSearch) ||
          !(searchChanged && !phoneSearch)) ? (
          <span>
            <IconButton
              disabled={!fullNameSearch && !truckNumberSearch}
              onClick={filterHandler}
            >
              <FilterListIcon />
            </IconButton>
          </span>
        ) : (
          <span>
            <IconButton onClick={filterClearHandler}>
              <FilterListOffIcon />
            </IconButton>
          </span>
        )}
      </Tooltip>
      <Box sx={{ p: 1, display: 'inline-flex' }}>
        <Tooltip title="Create">
          <span>
            <IconButton onClick={createHandler}>
              <AddIcon />
            </IconButton>
          </span>
        </Tooltip>
        <Tooltip title="Edit">
          <span>
            <IconButton disabled={selected.length !== 1} onClick={editHandler}>
              <EditIcon />
            </IconButton>
          </span>
        </Tooltip>
        <Tooltip title="Delete">
          <span>
            <IconButton
              disabled={selected.length !== 1}
              onClick={deleteHandler}
            >
              <DeleteIcon />
            </IconButton>
          </span>
        </Tooltip>
      </Box>
      <Loader visible={isLoading} />
    </Toolbar>
  );
}

export default function EnhancedTable() {
  const [order, setOrder] = React.useState<Order | undefined>('asc');
  const [orderBy, setOrderBy] = React.useState<keyof Driver | undefined>(
    'fullName',
  );
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = React.useState<number>(15);
  const [queryParams, setQueryParams] = React.useState<Record<string, any>>({});
  const tableContainerRef = React.useRef(null);
  const location = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading,
    isFetching,
    data: { items, total } = { items: [], total: 0 },
  } = useQuery({
    queryKey: ['drivers', page, rowsPerPage, orderBy, order, queryParams],
    queryFn: DriversQuery,
    keepPreviousData: true,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });
  const rows = items || [];

  React.useEffect(() => {
    if (tableContainerRef.current) {
      const container = tableContainerRef.current as HTMLElement;
      container.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    }
  }, [rows]);

  React.useEffect(() => {
    const newSelected: string[] = [];
    selected.forEach((selectedItem) => {
      const selectedRow = rows.find((row) => row.id === selectedItem);
      if (selectedRow) {
        newSelected.push(selectedItem);
      }
    });
    setSelected(newSelected);
  }, [rows]);

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof Driver,
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    const isDesc = orderBy === property && order === 'desc';
    if (isDesc) {
      setOrder(undefined);
      setOrderBy(undefined);
    } else {
      setOrder(isAsc ? 'desc' : 'asc');
      setOrderBy(property);
    }
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newSelected = rows.map((n: Driver) => n.id);
      setSelected(newSelected);
      return;
    }
    setSelected([]);
  };

  const handleClick = (
    event: React.MouseEvent<unknown>,
    item: (typeof rows)[number],
  ) => {
    navigate(`../${getPath(item)}/view/${item.id}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };

  const handleSelect = (event: React.MouseEvent<unknown>, name: string) => {
    event.stopPropagation();
    const selectedIndex = selected.indexOf(name);
    let newSelected: readonly string[] = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const isSelected = (name: string) => selected.indexOf(name) !== -1;

  return (
    <Box sx={{ width: '100%', height: '100%' }}>
      <Paper sx={{ width: '100%', height: '100%', backgroundColor: 'unset' }}>
        <EnhancedTableToolbar
          selected={selected}
          setQueryParams={(query) => {
            setQueryParams(query);
            setPage(0);
          }}
        />
        <TableContainer
          ref={tableContainerRef}
          sx={{ height: 'calc(100% - 115px)' }}
        >
          <Table
            sx={{ minWidth: 750 }}
            aria-labelledby="tableTitle"
            size="small"
            stickyHeader
          >
            <EnhancedTableHead
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            />
            <TableBody>
              {rows.map((row, index) => {
                const isItemSelected = isSelected(`${row.id}`);
                const labelId = `enhanced-table-checkbox-${index}`;
                return (
                  <TableRow
                    hover
                    onDoubleClick={(event) => handleClick(event, row)}
                    role="checkbox"
                    aria-checked={isItemSelected}
                    tabIndex={-1}
                    key={row.id}
                    selected={isItemSelected}
                    sx={{ cursor: 'pointer' }}
                  >
                    <StyledTableCell padding="checkbox">
                      <Checkbox
                        id={`${labelId}`}
                        color="primary"
                        checked={isItemSelected}
                        inputProps={{
                          'aria-labelledby': labelId,
                        }}
                        onClick={(event) => handleSelect(event, `${row.id}`)}
                      />
                    </StyledTableCell>
                    {headCells.map((cell) => {
                      const cellData = cell.getData(row);
                      return (
                        <StyledTableCell
                          key={cell.id}
                          title={cellData.title}
                          align={cell.numeric ? 'right' : 'center'}
                        >
                          {cellData.data}
                        </StyledTableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 15, 20, 25, 35, 50]}
          component="div"
          count={total}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      <Loader visible={isLoading || isFetching} />
    </Box>
  );
}
